import { fabric } from "fabric";


export class Table {
  private canvas: fabric.StaticCanvas;
  private cellWidth: number;
  private cellHeight: number;
  private row: number;
  private col: number;
  private cellLines = [];
  private mergeCellRects = [];
  private cellTexts = [];
  constructor(row: number, col: number, container: string) {
    this.canvas = new fabric.StaticCanvas(container);
    this.cellWidth = 120;
    this.cellHeight = 40;
    this.row = row;
    this.col = col;
  }

  public initMaskTable() {
    const totalHeight = this.row * this.cellHeight;
    const totalWidth = this.col * this.cellWidth;
    for (let i = 0; i <= this.row; i++) {
      let horizontalLine = new fabric.Line(
        [0, i * this.cellHeight, totalWidth, i * this.cellHeight],
        {
          stroke: "black",
          strokeWidth: 1,
        }
      );
      this.cellLines.push(horizontalLine);
    }

    // 使用另一个for循环创建列线条
    for (let j = 0; j <= this.col; j++) {
      var verticalLine = new fabric.Line(
        [j * this.cellWidth, 0, j * this.cellWidth, totalHeight],
        {
          stroke: "black",
          strokeWidth: 1,
        }
      );
      this.cellLines.push(verticalLine);
    }
    return this;
  }



  public mergeCells(
    startRow: number,
    startCol: number,
    endRow: number,
    endCol: number
  ) :Table{
    // 计算矩形的位置和大小
    const left = startCol * this.cellWidth;
    const top = startRow * this.cellHeight;
    const width = (endCol - startCol + 1) * this.cellWidth;
    const height = (endRow - startRow + 1) * this.cellHeight;

    // 创建一个矩形
    const rect = new fabric.Rect({
      left: left,
      top: top,
      width: width,
      height: height,
      fill: "white",
      stroke: "black",
      strokeWidth: 1,
    });
    var text = new fabric.Textbox("Hello", {
      left: rect.left,
      top: rect.top,
      width: rect.width,
      height: rect.height,
      fill: "black",
      lockMovementX: true,
      lockMovementY: true,
    });
    this.cellTexts.push(text);
    this.mergeCellRects.push(rect);
    return this;
  }

  public render() {
    console.log(this.cellLines, this.mergeCellRects, this.cellTexts);
    
    this.canvas.add.apply(this.canvas, [...this.cellLines,...this.mergeCellRects,...this.cellTexts]);
  }
}

export function initMaskTable(row: number, col: number, container: string) {
  // 初始化fabric.Canvas对象
  const canvas = new fabric.StaticCanvas("container");
  // 计算每个单元格的宽度和高度
  const cellWidth = 120;
  const cellHeight = 40;

  const totalHeight = row * cellHeight;
  const totalWidth = col * cellWidth;
  // 使用for循环创建行线条
  const lines: any[] = [];
  for (let i = 0; i <= row; i++) {
    let horizontalLine = new fabric.Line(
      [0, i * cellHeight, totalWidth, i * cellHeight],
      {
        stroke: "black",
        strokeWidth: 1,
      }
    );
    lines.push(horizontalLine);
  }

  // 使用另一个for循环创建列线条
  for (let j = 0; j <= col; j++) {
    var verticalLine = new fabric.Line(
      [j * cellWidth, 0, j * cellWidth, totalHeight],
      {
        stroke: "black",
        strokeWidth: 1,
      }
    );
    lines.push(verticalLine);
  }
  return function render() {
    canvas.add.apply(canvas, lines);
  };
}

export function mergeCells(
  startRow: number,
  startCol: number,
  endRow: number,
  endCol: number,
  canvas: fabric.StaticCanvas,
  cellWidth: number,
  cellHeight: number
) {
  // 计算矩形的位置和大小
  const left = startCol * cellWidth;
  const top = startRow * cellHeight;
  const width = (endCol - startCol + 1) * cellWidth;
  const height = (endRow - startRow + 1) * cellHeight;

  // 创建一个矩形
  const rect = new fabric.Rect({
    left: left,
    top: top,
    width: width,
    height: height,
    fill: "white",
    stroke: "black",
    strokeWidth: 1,
  });
  var text = new fabric.Textbox("Hello", {
    left: rect.left,
    top: rect.top,
    width: rect.width,
    height: rect.height,
    fill: "black",
    lockMovementX: true,
    lockMovementY: true,
    lockScalingX: true,
    lockScalingY: true,
    splitByGrapheme: true,
  });

  canvas.add(text);
  // 添加矩形到画布
  canvas.add(rect);
}
